import React, { Component } from 'react';
import classnames from 'classnames';
import PropTypes from 'prop-types';

class ProgressiveImage extends Component {
  static defaultProps = {
    alt: '',
  };

  constructor(props) {
    super(props);
    this.state = {
      loading: true,
      currentSrc: props.placeholder,
    };
  }

  componentDidMount() {
    const { src } = this.props;
    const imageToLoad = new Image();
    imageToLoad.src = src;
    imageToLoad.onload = () =>
      this.setState({ currentSrc: src, loading: false });
  }

  render() {
    const { currentSrc, loading } = this.state;
    const { src, placeholder, alt, className, ...rest } = this.props;
    return (
      <img
        src={currentSrc}
        className={classnames('ProgressiveImage', className)}
        style={{ opacity: loading ? 0.5 : 1 }}
        alt={alt}
        {...rest}
      />
    );
  }
}

ProgressiveImage.propTypes = {
  src: PropTypes.string.isRequired,
  placeholder: PropTypes.string.isRequired,
  alt: PropTypes.string.isRequired,
  className: PropTypes.string,
};

export default ProgressiveImage;
