import React, { Component } from 'react';
import { Document, Page } from 'react-pdf/dist/entry.webpack';
import throttle from 'lodash.throttle';
import classnames from 'classnames';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faArrowCircleDown } from '@fortawesome/free-solid-svg-icons';
import { MainContent, Loading } from './layouts';
import CVWeb from './documents/CV-Haseeb-Web.pdf';
import CV from './documents/CV-Haseeb.pdf';
import ResumeError from './misc/ResumeError';

class Resume extends Component {
  constructor(props) {
    super(props);
    this.state = {
      PDFWidth: null,
      loading: true,
      loadingError: null,
    };
    this.widthMaker = React.createRef();
    this.setPDFWidth = this.setPDFWidth.bind(this);
    this.throttledSetPDFWidth = throttle(this.setPDFWidth, 500);
    this.onDocumentLoadSuccess = this.onDocumentLoadSuccess.bind(this);
    this.onDocumentLoadError = this.onDocumentLoadError.bind(this);
  }

  componentDidMount() {
    document.title = 'Resume - Haseeb Anwar';
    this.setPDFWidth();
    window.addEventListener('resize', this.throttledSetPDFWidth);
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.throttledSetPDFWidth);
  }

  setPDFWidth() {
    if (!this.state.loadingError) {
      const width = this.widthMaker.current.offsetWidth;
      this.setState({ PDFWidth: width });
    }
  }

  onDocumentLoadSuccess() {
    this.setState({
      loading: false,
    });
  }

  onDocumentLoadError(error) {
    this.setState({
      loading: false,
      loadingError: <ResumeError CV={CV} />,
    });
    console.log(error.message);
  }

  render() {
    const { PDFWidth, loading, loadingError } = this.state;
    if (loadingError) return loadingError;
    return (
      <MainContent>
        <div className="Resume" ref={this.widthMaker}>
          {loading && <Loading />}
          <div className={classnames({ 'd-none': loading })}>
            <h1>My Resume</h1>
            <a href={CV} className="btn btn-primary mt-4" download>
              <FontAwesomeIcon icon={faArrowCircleDown} className="mr-2" />
              Download
            </a>
            <div className="PDFWrapper">
              <Document
                file={CVWeb}
                onLoadSuccess={this.onDocumentLoadSuccess}
                onLoadError={this.onDocumentLoadError}
                error=""
              >
                <Page pageNumber={1} width={PDFWidth} scale={0.95} />
                <hr />
                <Page pageNumber={2} width={PDFWidth} scale={0.95} />
              </Document>
            </div>
          </div>
        </div>
      </MainContent>
    );
  }
}

export default Resume;
