import React, { Component } from 'react';
import { Context } from './context';
import { faChevronLeft } from '@fortawesome/free-solid-svg-icons';
import NotFound from './NotFound';
import { MainContent, Project, ButtonLink } from './layouts';

class ProjectDetails extends Component {
  render() {
    const { projectId } = this.props.match.params;
    const [project] = this.context.getProject(projectId);
    if (!project) {
      return <NotFound />;
    }
    return (
      <MainContent>
        <div className="ProjectDetails">
          <ButtonLink
            icon={faChevronLeft}
            onClick={() => this.props.history.push('/portfolio')}
          >
            Back to Portfolio
          </ButtonLink>
          <Project project={project} detailed />
        </div>
      </MainContent>
    );
  }
}

ProjectDetails.contextType = Context;

export default ProjectDetails;
