import React, { Component, Fragment } from 'react';
import { Link } from 'react-router-dom';
import classnames from 'classnames';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faInfoCircle, faLink } from '@fortawesome/free-solid-svg-icons';
import { Badge, ProgressiveImage, Gallery } from './';
import PropTypes from 'prop-types';

class Project extends Component {
  constructor(props) {
    super(props);
    this.renderTechnologies = this.renderTechnologies.bind(this);
  }

  renderTechnologies() {
    const { detailed } = this.props;
    const { technologies } = this.props.project;
    return (
      <div className={classnames('technologies', { 'on-detailed': detailed })}>
        <h6 className="title mb-3">Technologies</h6>
        {technologies.map((technology) => (
          <Badge
            key={technology.name}
            name={technology.name}
            icon={technology.icon}
          />
        ))}
      </div>
    );
  }

  render() {
    const { detailed } = this.props;
    const {
      id,
      title,
      subtitle,
      image,
      imageMin,
      introContent,
      content,
      liveLink,
      gallery,
      galleryMin,
    } = this.props.project;
    return (
      <div className="Project">
        <h3 className="title">
          {detailed ? title : <Link to={`/portfolio/${id}`}>{title}</Link>}
        </h3>
        {subtitle && <h5 className="subtitle">{subtitle}</h5>}
        <div className="row mt-md-3">
          <div className="col-md-6 content">
            {detailed ? (
              content
            ) : (
              <Fragment>
                {introContent}
                {this.renderTechnologies()}
              </Fragment>
            )}
            <div className="btns mt-3">
              {!detailed && (
                <Link to={`/portfolio/${id}`} className="btn btn-primary mr-2">
                  <FontAwesomeIcon icon={faInfoCircle} className="mr-2" />
                  Details
                </Link>
              )}
              {liveLink && (
                <a
                  href={liveLink}
                  className="btn btn-primary"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  <FontAwesomeIcon icon={faLink} className="mr-2" />
                  View Online
                </a>
              )}
            </div>
          </div>
          <div className="col-md-6 image-container">
            {detailed && this.renderTechnologies()}
            <ProgressiveImage
              src={image}
              placeholder={imageMin}
              className={classnames('shadow-sm', { 'mt-md-4': detailed })}
            />
          </div>
        </div>
        {detailed && <Gallery images={gallery} imagesMin={galleryMin} />}
      </div>
    );
  }
}

Project.propTypes = {
  project: PropTypes.object.isRequired,
  detailed: PropTypes.bool,
};

export default Project;
