import validator from 'validator';

/*
 * This class contains methods for validating fields using 'validator.js' library methods
 * The methods return error message if validation failed and false otherwise
 */

class ValidateFields {
  validateName(name) {
    if (validator.isEmpty(name)) {
      return 'Name is required';
    }
    return false;
  }

  validateEmail(email) {
    if (validator.isEmpty(email)) {
      return 'Email is required';
    } else if (!validator.isEmail(email)) {
      return 'Invalid Email';
    }
    return false;
  }

  validateMessage(message) {
    if (validator.isEmpty(message)) {
      return 'Message is required';
    } else if (!validator.isLength(message, { min: 20 })) {
      return 'Message must contain at least 20 characters';
    }
    return false;
  }
}

const validateFields = new ValidateFields();

export { validateFields };
