import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { MainContent, Underline } from './layouts';

class Home extends Component {
  componentDidMount() {
    document.title = 'Haseeb Anwar | Full-Stack Web Developer';
  }

  render() {
    return (
      <MainContent style={{ background: 'transparent' }}>
        <div className="Home">
          <div className="headings">
            <h1 className="pre-title">Hi there,</h1>
            <h1 className="title display-4">I'm Haseeb</h1>
            <h1 className="post-title">A Passionate Full-Stack Developer</h1>
            <Underline width="70px" className="mt-4" />
          </div>
          <div className="mt-5">
            <Link to="/portfolio" className="btn btn-primary">
              See My Portfolio
            </Link>
          </div>
        </div>
      </MainContent>
    );
  }
}

export default Home;
