import React, { Component } from 'react';
import { ReactSVG } from 'react-svg';
import { MainContent } from '../layouts';
import plug from '../images/icons/plug.svg';
import PropTypes from 'prop-types';

class ResumeError extends Component {
  render() {
    const { CV } = this.props;
    return (
      <MainContent>
        <div className="ResumeError">
          <ReactSVG src={plug} />
          <p className="mb-0">An error has occurred while rendering Resume.</p>
          <p>
            Please{' '}
            <a href={CV} download>
              download
            </a>{' '}
            or try refreshing page.
          </p>
        </div>
      </MainContent>
    );
  }
}

ResumeError.propTypes = {
  CV: PropTypes.string.isRequired,
};

export default ResumeError;
