import React, { Component } from 'react';
import emailjs from 'emailjs-com';
import classnames from 'classnames';
import { v4 as uuid } from 'uuid';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
  faUser,
  faAt,
  faEnvelope,
  faPaperPlane,
  faCircleNotch,
} from '@fortawesome/free-solid-svg-icons';
import { validateFields } from '../utils/Validation';
import { Toast } from '../layouts';

const initialState = {
  name: {
    value: '',
    validateOnChange: false,
    error: '',
  },
  email: {
    value: '',
    validateOnChange: false,
    error: '',
  },
  message: {
    value: '',
    validateOnChange: false,
    error: '',
  },
  submitCalled: false,
  sendingMessage: false,
  sendStatus: {
    type: null,
    msg: null,
  },
};

class ContactForm extends Component {
  constructor(props) {
    super(props);
    this.state = initialState;
    this.handleSubmit = this.handleSubmit.bind(this);
    this.setSumbitCalled = this.setSumbitCalled.bind(this);
    this.sendMessage = this.sendMessage.bind(this);
    this.clearToastData = this.clearToastData.bind(this);
  }

  /*
   * validates the field onBlur if sumbit button is not clicked
   * set the validateOnChange to true for that field
   * check for error
   */
  handleBlur(validationFunc, evt) {
    const field = evt.target.name;
    // validate onBlur only when validateOnChange for that field is false
    // because if validateOnChange is already true there is no need to validate onBlur
    if (
      this.state[field]['validateOnChange'] === false &&
      this.state.submitCalled === false
    ) {
      this.setState((state) => ({
        [field]: {
          ...state[field],
          validateOnChange: true,
          error: validationFunc(state[field].value),
        },
      }));
    }
    return;
  }

  /*
   * update the value in state for that field
   * check for error if validateOnChange is true
   */
  handleChange(validationFunc, evt) {
    const field = evt.target.name;
    const fieldVal = evt.target.value;
    this.setState((state) => ({
      [field]: {
        ...state[field],
        value: fieldVal,
        error: state[field]['validateOnChange'] ? validationFunc(fieldVal) : '',
      },
    }));
  }

  /*
   * validate all fields
   * check if all fields are valid if yes then submit the Form
   * otherwise set errors for the feilds in the state
   */
  handleSubmit(evt) {
    evt.preventDefault();
    // validate all fields
    const { name, email, message } = this.state;
    const nameError = validateFields.validateName(name.value);
    const emailError = validateFields.validateEmail(email.value);
    const messageError = validateFields.validateMessage(message.value);
    if ([nameError, emailError, messageError].every((e) => e === false)) {
      // no errors submit the form
      this.setState({ sendingMessage: true }, () => {
        this.sendMessage({
          name: name.value,
          email: email.value,
          message: message.value,
        });
      });
    } else {
      // update the state with errors
      this.setState((state) => ({
        name: {
          ...state.name,
          validateOnChange: true,
          error: nameError,
        },
        email: {
          ...state.email,
          validateOnChange: true,
          error: emailError,
        },
        message: {
          ...state.message,
          validateOnChange: true,
          error: messageError,
        },
      }));
    }
  }

  async sendMessage(templateParams) {
    try {
      await emailjs.send(
        'sendgrid',
        'contact_form',
        templateParams,
        'user_nRo30l7nusvq8z7EyKajd'
      );
      this.setState({
        ...initialState,
        sendStatus: {
          type: 'success',
          msg: 'Your message has been sent',
        },
      });
    } catch (error) {
      this.setState({
        sendStatus: {
          type: 'error',
          msg: 'Failed to send the message',
        },
        sendingMessage: false,
      });
      console.log(error);
    }
  }

  clearToastData() {
    this.setState({ sendStatus: initialState.sendStatus });
  }

  setSumbitCalled() {
    this.setState({ submitCalled: true });
  }

  render() {
    const { email, name, message, sendingMessage, sendStatus } = this.state;
    return (
      <form onSubmit={this.handleSubmit} className="ContactForm" noValidate>
        {sendStatus.type && (
          <Toast
            key={uuid()}
            type={sendStatus.type}
            title={sendStatus.msg}
            isStatic={sendStatus.type === 'error'}
            clearToastData={this.clearToastData}
          />
        )}
        <div className="form-row">
          <div className="form-group col-md-6">
            <div className="input-group">
              <div className="input-group-prepend">
                <span
                  className="input-group-text"
                  id="inputGroup-sizing-default"
                >
                  <FontAwesomeIcon icon={faUser} />
                </span>
              </div>
              <input
                className={classnames(
                  'form-control',
                  { 'is-valid': name.error === false },
                  { 'is-invalid': name.error }
                )}
                type="text"
                name="name"
                value={name.value}
                placeholder="Enter your name"
                onChange={(evt) =>
                  this.handleChange(validateFields.validateName, evt)
                }
                onBlur={(evt) =>
                  this.handleBlur(validateFields.validateName, evt)
                }
              />
              <div className="invalid-feedback">{name.error}</div>
            </div>
          </div>

          <div className="form-group col-md-6">
            <div className="input-group">
              <div className="input-group-prepend">
                <span
                  className="input-group-text"
                  id="inputGroup-sizing-default"
                >
                  <FontAwesomeIcon icon={faAt} />
                </span>
              </div>
              <input
                className={classnames(
                  'form-control',
                  { 'is-valid': email.error === false },
                  { 'is-invalid': email.error }
                )}
                type="email"
                name="email"
                value={email.value}
                placeholder="Enter your email"
                onChange={(evt) =>
                  this.handleChange(validateFields.validateEmail, evt)
                }
                onBlur={(evt) =>
                  this.handleBlur(validateFields.validateEmail, evt)
                }
              />
              <div className="invalid-feedback">{email.error}</div>
            </div>
          </div>
        </div>

        <div className="form-group">
          <div className="input-group">
            <div className="input-group-prepend">
              <span className="input-group-text" style={{ display: 'block' }}>
                <FontAwesomeIcon icon={faEnvelope} />
              </span>
            </div>
            <textarea
              className={classnames(
                'form-control',
                { 'is-valid': message.error === false },
                { 'is-invalid': message.error }
              )}
              name="message"
              value={message.value}
              placeholder="Put your message here"
              rows={5}
              onChange={(evt) =>
                this.handleChange(validateFields.validateMessage, evt)
              }
              onBlur={(evt) =>
                this.handleBlur(validateFields.validateMessage, evt)
              }
            ></textarea>
            <div className="invalid-feedback">{message.error}</div>
          </div>
        </div>
        <div className="mt-4">
          <button
            type="submit"
            className="btn btn-primary"
            onMouseDown={this.setSumbitCalled}
            disabled={sendingMessage}
          >
            <FontAwesomeIcon
              icon={!sendingMessage ? faPaperPlane : faCircleNotch}
              spin={sendingMessage}
              className="mr-2"
            />
            Send Message
          </button>
        </div>
      </form>
    );
  }
}

export default ContactForm;
