import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { ReactSVG } from 'react-svg';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
  faFacebook,
  faGithub,
  faMedium,
} from '@fortawesome/free-brands-svg-icons';
import { faAt } from '@fortawesome/free-solid-svg-icons';
import Navbar from './misc/Navbar';
import avatar from './images/avatar.jpg';
import fiverr from './images/icons/fiverr.svg';

class Sidebar extends Component {
  render() {
    return (
      <div className="Sidebar col-md-3 py-4">
        <Link to="/" className="img-name-wrapper">
          <div className="img-container shadow-lg">
            <img src={avatar} alt="Haseeb" />
          </div>
          <div className="intro-content text-center mt-3">
            <h1 className="name">Haseeb Anwar</h1>
            <h2 className="subtitle">Full-Stack Web Developer</h2>
          </div>
        </Link>
        <div className="main mt-md-4 px-3">
          <Navbar />
          <div className="reach-me mt-md-4 mt-2">
            <p className="reach-me-text">
              <small>Reach me at</small>
            </p>
            <div className="social-icons">
              <a
                href="mailto:haseebanwar99@gmail.com"
                title="Mail: haseebanwar99@gmail.com"
              >
                <FontAwesomeIcon icon={faAt} />
              </a>
              <a
                href="https://facebook.com/haseebanwar99"
                title="Facebook: haseebanwar99"
                target="_blank"
                rel="noopener noreferrer"
              >
                <FontAwesomeIcon icon={faFacebook} />
              </a>
              <a
                href="https://github.com/haseeb99"
                title="GitHub: haseeb99"
                target="_blank"
                rel="noopener noreferrer"
              >
                <FontAwesomeIcon icon={faGithub} />
              </a>
              <a
                href="https://fiverr.com/haseebanwar99"
                title="Fiverr: haseebanwar99"
                target="_blank"
                rel="noopener noreferrer"
              >
                <ReactSVG
                  src={fiverr}
                  wrapper="span"
                  beforeInjection={(svg) => {
                    svg.classList.add('svg-inline');
                  }}
                />
              </a>
              <a
                href="https://medium.com/@haseebanwar99"
                title="Medium: @haseebanwar99"
                target="_blank"
                rel="noopener noreferrer"
              >
                <FontAwesomeIcon icon={faMedium} />
              </a>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Sidebar;
