import React, { Component } from 'react';
import { MainContent } from './layouts';
import ContactForm from './misc/ContactForm';

class Contact extends Component {
  componentDidMount() {
    document.title = 'Contact - Haseeb Anwar';
  }

  render() {
    return (
      <MainContent>
        <div className="Contact position-relative">
          <h1>Contact Me</h1>
          <p>
            Have an idea to be implemented or want to work together? Please
            reach out to{' '}
            <a
              href="mailto:haseebanwar99@gmail.com"
              title="Mail: haseebanwar99@gmail.com"
            >
              haseebanwar99@gmail.com
            </a>{' '}
            or leave me a message below and I will get back to you soon.
          </p>
          <ContactForm />
        </div>
      </MainContent>
    );
  }
}

export default Contact;
