import React, { Component } from 'react';
import Lightbox from 'react-image-lightbox';
import { ReactSVG } from 'react-svg';
import { Overlay, ProgressiveImage } from './';
import PropTypes from 'prop-types';

import zoomInIcon from '../images/icons/zoom-in.svg';
import 'react-image-lightbox/style.css';

class Gallery extends Component {
  constructor(props) {
    super(props);
    this.state = {
      open: false,
      photoIndex: 0,
    };
  }

  renderImageGallery() {
    const { images, imagesMin } = this.props;
    let imagesInAColumn = images.length / 3;
    let imagesInAColumnInt = Math.floor(imagesInAColumn);

    // the zeroth index value refers to the no. of images in column 1 and so on
    let totalImagesInRespectiveColumns = [
      imagesInAColumnInt,
      imagesInAColumnInt,
      imagesInAColumnInt,
    ];

    /*
     * if no. of images in a single column is not integer then make some space for extra images
     * suppose there are 14 images then 14/3=4.66 which means there are two extra images
     * one is pushed to the first column and second is pushed to the second column so the array will look like this
     * [5, 5, 4]
     * i.e 5 images in column 1, 5 images in column 2 and 4 images in column 4 = 14 images
     */
    if (imagesInAColumn % 1 !== 0) {
      totalImagesInRespectiveColumns =
        Math.round(imagesInAColumn) - Math.floor(imagesInAColumn) === 1
          ? [imagesInAColumnInt + 1, imagesInAColumnInt + 1, imagesInAColumnInt]
          : [imagesInAColumnInt, imagesInAColumnInt + 1, imagesInAColumnInt];
    }
    let column = [];
    for (let i = 0; i < 3; i++) {
      let imagesForOneColumn = [];
      let startIndex = totalImagesInRespectiveColumns.reduce(
        (acc, cur, idx) => (idx < i ? acc + cur : acc + 0),
        0
      );
      let endIndex = totalImagesInRespectiveColumns.reduce((acc, cur, idx) =>
        idx < i + 1 ? acc + cur : acc + 0
      );
      for (let j = startIndex; j < endIndex; j++) {
        imagesForOneColumn.push(
          <div
            key={j}
            className="img-container shadow-sm"
            onClick={() => this.setState({ open: true, photoIndex: j })}
          >
            <Overlay opacity={0.5} />
            <ReactSVG src={zoomInIcon} wrapper="span" className="zoom-in" />
            <ProgressiveImage src={images[j]} placeholder={imagesMin[j]} />
          </div>
        );
      }
      // if there are images then make a column of images
      if (imagesForOneColumn.length > 0) {
        column.push(
          <div key={i} className="column col-md-4">
            {imagesForOneColumn}
          </div>
        );
      }
    }
    return column;
  }

  render() {
    const { open, photoIndex } = this.state;
    const { images } = this.props;
    return (
      <div className="Gallery mt-5">
        <h3>Gallery</h3>
        <div className="container">
          <div className="row">{this.renderImageGallery()}</div>
        </div>
        {open && (
          <Lightbox
            wrapperClassName="full-screen-wrapper"
            mainSrc={images[photoIndex]}
            nextSrc={images[(photoIndex + 1) % images.length]}
            prevSrc={images[(photoIndex + images.length - 1) % images.length]}
            onMovePrevRequest={() =>
              this.setState((state) => ({
                photoIndex:
                  (state.photoIndex + images.length - 1) % images.length,
              }))
            }
            onMoveNextRequest={() =>
              this.setState((state) => ({
                photoIndex: (state.photoIndex + 1) % images.length,
              }))
            }
            onCloseRequest={() => this.setState({ open: false })}
          />
        )}
      </div>
    );
  }
}

Gallery.propTypes = {
  images: PropTypes.array.isRequired,
  imagesMin: PropTypes.array.isRequired,
};

export default Gallery;
