import React, { Component, Fragment } from 'react';
import { Context } from './context';
import { MainContent, PostCard } from './layouts';

class Blog extends Component {
  componentDidMount() {
    document.title = 'Blog - Haseeb Anwar';
  }

  render() {
    const { blogPosts } = this.context;
    return (
      <MainContent>
        <div className="Blog">
          <h1>Blog</h1>
          <p>
            I love to share what I know. So, in the free time I usually write
            articles on JavaScript and family.
          </p>
          {blogPosts.map((post, i) => (
            <Fragment key={post.title}>
              <PostCard post={post} />
              {i < blogPosts.length - 1 && <hr />}
            </Fragment>
          ))}
        </div>
      </MainContent>
    );
  }
}

Blog.contextType = Context;

export default Blog;
