import React, { Component } from 'react';
import { Context } from './context';
import { MainContent, SkillProgress } from './layouts';

class Expertise extends Component {
  constructor(props) {
    super(props);
    this.renderExpertise = this.renderExpertise.bind(this);
  }

  componentDidMount() {
    document.title = 'Expertise - Haseeb Anwar';
  }

  renderExpertise() {
    const { expertise } = this.context;
    return expertise.map((experty) => (
      <div key={experty.name} className="col-lg-3 col-md-4 col-6">
        <SkillProgress
          name={experty.name}
          icon={experty.icon}
          score={experty.score}
        />
      </div>
    ));
  }

  render() {
    return (
      <MainContent>
        <div className="Expertise">
          <h1>Expertise</h1>
          <p>
            Software engineering has a ton of technologies and processes to
            develop, deploy, and maintain software applications. I keep
            knowledge of the following technologies.
          </p>
          <div className="container">
            <div className="row expertise-wrapper">
              {this.renderExpertise()}
            </div>
          </div>
        </div>
      </MainContent>
    );
  }
}

Expertise.contextType = Context;

export default Expertise;
