import React, { Component, Fragment } from 'react';
import { Context } from './context';
import { MainContent, Project } from './layouts';

class Portfolio extends Component {
  componentDidMount() {
    document.title = 'Portfolio - Haseeb Anwar';
  }

  render() {
    const { projects } = this.context;
    return (
      <MainContent>
        <div className="Portfolio">
          <h1>Portfolio</h1>
          <p>
            Some of my projects are listed here. Each project includes all the
            details about technologies and methods used during the project life
            cycle. All of them were completely made by me.
          </p>
          {projects.map((project, i) => (
            <Fragment key={project.id}>
              <Project project={project} />
              {i < projects.length - 1 && <hr />}
            </Fragment>
          ))}
        </div>
      </MainContent>
    );
  }
}

Portfolio.contextType = Context;

export default Portfolio;
